<script>
import RoleCollection from "@/collections/RoleCollection";
import HospitalCollection from "@/collections/HospitalCollection";
import ClinicCollection from "@/collections/ClinicCollection";
import User from "@/models/User";
import ModelRequest from "@/request/ModelRequest";

export default {
  name: "UserCreate",
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.users"), route: "admin.users" },
        {
          title: this.$t("models.app.add"),
          active: true,
        },
      ],
      roles: new RoleCollection(),
      hospitals: new HospitalCollection(),
      clinics: new ClinicCollection(),
      user: new User(),
      request: new ModelRequest(),
    };
  },
  created() {
    this.roles.fetchAll();

    this.hospitals.fetchAll();

    this.clinics.fetchAll();

    this.request.bind(this.user);
  },
  methods: {
    submit() {
      this.request.store({
        redirect: { name: "admin.users" },
      });
    },
  },
};
</script>